body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: -webkit-repeating-linear-gradient(
  45deg,
  #262626,
  #262626 80px,
  #0D0C13 80px,
  #0D0C13 160px
);
  background: repeating-linear-gradient(
  45deg,
  #262626,
  #262626 80px,
  #0D0C13 80px,
  #0D0C13 160px
);
  color: white;
  text-align: center;
}
.contentContainer {
  margin: 83px auto 0;
  width: 90%;
  background: #0D0C13;
}
@media (max-width: 740px) {
  .contentContainer {
    margin: 60px auto 0;
  }
}
@media (max-width: 580px) {
  .contentContainer {
    margin: 40px auto 0;
  }
}
@media (max-width: 480px) {
  .contentContainer {
    width: 95%;
  }
  p {
    font-size: 13px;
  }
}

.navBarContainer {
	z-index: 10;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background: #4C0020;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,1);
	cursor: pointer;
}
.navBarContainer p {
	margin: 0;
	font-size: 60px;
}
.navBarContainer img {
	width: 100px;
}
@media (max-width: 740px) {
	.navBarContainer p {
		font-size: 45px;
	}
	.navBarContainer img {
		width: 70px;
	}
}
@media (max-width: 580px) {
	.navBarContainer p {
		font-size: 30px;
	}
	.navBarContainer img {
		width: 50px;
	}
}
.mapListContainer h1 {
	border-bottom: 2px solid white; 
}
.mapList {
	margin: 0 10%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}
.welcome {
	padding-top: 30px;
}
.logo {
	width: 350px;
}
@media (max-width: 980px) {
	.mapList {
		margin: 0;
	}
}
@media (max-width: 480px) {
	.logo {
		width: 280px;
	}
}

.map {
	margin: 20px;
	position: relative;
	height: 150px;
	width: 300px;
	border: 2px solid #8F003D;
}
.map img {
	height: 100%;
	width: 100%;
	cursor: pointer;
}
.map p {
	position: absolute;
	margin: 0;
	bottom: 0;
	font-size: 20px;
	left: 50%;
	width: 100%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	text-align: center;
	background: rgba(0,0,0,0.7);
	cursor: pointer;
}

@media (max-width: 980px) {
	.map {
		margin: 10px;
	}
}
.headerContainer {
	width: 100%;
	height: 650px;
	position: relative;
}
.headerContainer h1 {
	position: absolute;
	width: 100%;
	margin: 0;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	text-align: center;
	font-size: 70px;
	background: rgba(0,0,0,0.7);
}

@media (max-width: 1280px) {
	.headerContainer {
		height: 500px;
	}
	.headerContainer h1 {
		font-size: 50px;
	}
}
@media (max-width: 1060px) {
	.headerContainer {
		height: 400px;
	}
}
@media (max-width: 750px) {
	.headerContainer {
		height: 300px;
	}
}
@media (max-width: 480px) {
	.headerContainer {
		height: 200px;
	}
	.headerContainer h1 {
		font-size: 30px;
	}
}

.slide-arrow {
	border: solid #8E003B;
    border-width: 0 8px 8px 0;
    display: inline-block;
    padding: 3px;
    height: 4px;
    width: 4px;
    cursor: pointer;
}
.slide-arrow:hover {
	-webkit-transition: 0.3s;
	transition: 0.3s;
	border-color: #4F0021;
}
.right {
	position: absolute;
	right: 0;
	bottom: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
}
.left {
	position: absolute;
	left: 0;
	bottom: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
}
@media (max-width: 480px) {
	.slide-arrow {
		border-width: 0 8px 8px 0;
		height: 8px;
		width: 8px;
	}
	.right {
		right: -8px;
	}
	.left {
		left: -8px;
	}
}
.scroll {
	overflow-y: scroll;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	overflow-y: scroll;
	border: 5px solid black; 
	height: 650px;
	max-width: 700px;
	margin: 10px 25px;
}
.scroll::-webkit-scrollbar {
    width: 1em;
}
 
.scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
 
.scroll::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}
@media (max-width: 1060px) {
	.scroll {
		margin: 0 25px;
		height: 400px;
		max-width: none;
	}
}
@media (max-width: 480px) {
	.scroll {
		margin: 0 15px;
		height: 250px;
	}
}
.floorNav {
	position: absolute;
	right: 25px;
	top: -33px;
	display: flex;
	flex-direction: row;
}
.floorNav li {
	cursor: pointer;
	list-style-type: none;
	padding: 10px 20px;
}
.floorNav li:hover {
	-webkit-transition: ease 0.3s;
	transition: ease 0.3s;
	background: #4F0021;
}
.fullBorder {
	border: 2px solid black;
}
.noBorderLeft {
	border: 2px solid black;
	border-left: none;
}
.desktopSkewedNavButton {
	position: relative;
	z-index: 2;
	right: 70px;
	-webkit-transform: skewX(-30deg);
	        transform: skewX(-30deg);
}
.desktopSkewedNavButton a {
	-webkit-transform: skewX(30deg);
	        transform: skewX(30deg); 
	display: block;
}
.desktopUnskewedNavButton {
	position: absolute;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	right: 0;
	width: 50px;
}
.floorNavButton {
	background: #8A003B;
	/*possibly use #7D0035*/
}
.floorNavButton-active {
	-webkit-transition: ease 0.3s;
	transition: ease 0.3s;
	background: #4F0021;
}
.floorNavMobile {
	display: flex;
	flex-direction: row;
	justify-content: center;
	list-style-type: none;
}
.floorNavMobile li {
	cursor: pointer;
	padding: 10px 20px;
}
.floorNavMobile li:hover {
	-webkit-transition: ease 0.3s;
	transition: ease 0.3s;
	background: #4F0021;	
}
@media (max-width: 1060px) {
	.bottomFloorSkew {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		left: 20px;
		-webkit-transform: skewX(-30deg);
		        transform: skewX(-30deg);
		width: 40px;
	}
	.bottomFloorSkew a {
		-webkit-transform: skewX(30deg);
		        transform: skewX(30deg);
		display: block;
	}
	.topFloorSkew {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		right: 20px;
		width: 40px;
		-webkit-transform: skewX(30deg);
		        transform: skewX(30deg);
	}
	.topFloorSkew a {
		-webkit-transform: skewX(-30deg);
		        transform: skewX(-30deg);
		display: block;
	}
	.zIndex1 {
		z-index: 1;
	}
	.desktop {
		display: none;
	}
	.phone {
		display: flex;
	}
}
@media(max-width: 480px) {
	.floorNavMobile li {
		padding: 5px 10px;
	}
}

.lightBoxContainer {
	display: none;
}
.lightBoxContainer-active {
	z-index: 100;
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	background: rgba(0,0,0,0.9);
	cursor: pointer;
}
.lightBoxPictureContainer {
	margin: 15px auto;
	display: flex;
	flex-direction: column;
}
.lightBoxPicture {
	width: 100vw;
}

.imageContainer {
	display: flex;
	flex-direction: column;
}
.imageContainer img {
	margin: 10px;
	height: auto;
	width: 95%;
	cursor: pointer;
}

@media (max-width: 480px) {
	.imageContainer img {
		margin: 5px;
	}
}
.dotsContainer {
	display: flex;
	flex-direction: row;
	margin-left: 25px;
}
.dot {
	height: 15px;
	width: 15px;
	border-radius: 50%;
	background: #8E003B;
	margin: 5px;
	margin-bottom: 15px;
	cursor: pointer;
}
.dot:hover {
	-webkit-transition: 0.3s;
	transition: 0.3s;
	background: #4F0021;
}
.dot-active {
	-webkit-transition: 0.3s;
	transition: 0.3s;
	background: #4F0021;
}

@media (max-width: 1060px) {
	.dot {
		margin-bottom: 5px;
	}
}

.carouselContainer {
	display: flex;
	flex-direction: column;
	position: relative;
	margin: 0 10px;
}
.anglesContainer {
	margin-top: 45px;
	display: flex;
	flex-direction: row;
	justify-content: center;
}
.anglesText {
	margin: 20px;
}
.textContainer {
	position: relative;
}
.desktop {
	display: flex;
}
.phone {
	display: none;
}
@media (max-width: 1060px) {
	.anglesContainer {
		flex-direction: column;
		margin-top: 0;
	}
	.anglesText {
		margin: 0px 20px;
	}
	.desktop {
		display: none;
	}
	.phone {
		display: flex;
	}
}

